import sunuiStar from '../../../components/sunui-star/sunui-star.vue'
let i = 0;
import { mapState,mapGetters } from 'vuex';
export default {
	data() {
		return {
			taskCount: '',
			tiemCount: '',
			starCount: ''
		};
	},
	onLoad() {
		this.numDH();
		console.log(this.user_info)
	},
	components:{
		sunuiStar
	},
	computed:{
		...mapState(['user_info']),
		...mapGetters(['id']),
		avatarUrl(){
			return `url(${this.user_info.avatarUrl})`
		},
		my_info_nav(){
			if(this.id){
				return '/pages/tabbar/my/myInfo/myInfo'
			}else{
				return '/pages/tabbar/my/myInfo/createInfo'
			}
		}
	},
	methods: {
		numDH() {
			if (i < 20) {
				setTimeout(() => {
					this.taskCount = i;
					this.tiemCount = i;
					this.starCount = i;
					i++;
					this.numDH();
				}, 20);
			} else {
				this.taskCount = this.coutNum(1000);
				this.tiemCount = this.coutNum(2000);
				this.starCount = this.coutNum(300);
			}
		},
		coutNum(e) {
			if (e > 1000 && e < 10000) {
				e = (e / 1000).toFixed(1) + 'k';
			}
			if (e > 10000) {
				e = (e / 10000).toFixed(1) + 'W';
			}
			return e;
		}
	}
};
